@font-face {
  font-family: Ferry;
  src: url('./fonts/ferry_black.otf');
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


html,
body,
#root {
  height: 100%;
}


body {
  margin: 0;
  font-family: Inter, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.scroll-hidden {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1330px;
  padding: 0px 10px;
}

.slick-list {
  margin: 0 -20px;
}

.slick-slide>div {
  margin: 0 20px;
}

.top {
  padding-top: 58px;

  .top_wrapper {
    position: relative;
    display: flex;

    .top_left {
      width: 56%;

      .top_title {
        font-family: Ferry;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-size: 60px;
      }

      .top_text {
        margin-top: 24px;
        margin-bottom: 72px;
        font-size: 20px;
        width: 534px;
      }
    }

    .top_right {
      position: absolute;
      z-index: -1;
      top: -39px;
      right: 9px;
      width: 46%;
      height: 629px;
      background-image: url('../src/img/top-right-img.png');
      background-repeat: no-repeat;
      background-size: auto;
    }
  }
}

.title {
  margin-bottom: 56px;
  font-family: Ferry;
  font-size: 45px;
}

.center {
  padding-top: 194px;

  .center_bottom {
    margin-bottom: 78px;
    margin-top: 51px;
    width: 100%;
    height: 600px;
    background-image: url('../src/img/center-bottom.png');
    background-size: auto;
    background-position: 0px 0px;
    background-repeat: no-repeat;
  }
}

.bottom {
  padding-bottom: 114px;

  .cards_wripper {
    padding-top: 19px;
    display: flex;
    justify-content: space-between;
  }
}

@media (max-width: 1346px) {
  .container {
    max-width: 1150px;
  }

  .top {
    .top_wrapper {
      .top_left {
        width: 56%;

        .top_title {
          font-size: 52px;
        }
      }

      .top_right {
        top: -20px;
        background-size: 100%;
      }
    }
  }

  .center {
    padding-top: 64px;

    .center_bottom {
      background-size: 100%;
      margin-bottom: 20px;
    }
  }

  .title {
    margin-bottom: 30px;
  }
}

@media (max-width: 1172px) {
  .container {
    max-width: 1000px;
  }

  .top {
    .top_wrapper {
      .top_left {
        width: 56%;

        .top_title {
          font-size: 45px;
        }

        .top_text {
          margin-bottom: 29px;
          font-size: 24px;
          width: 78%;
        }
      }

      .top_right {
        background-size: 100%;
      }
    }
  }

  .center {
    .center_bottom {
      max-height: 500px;
      margin-top: 9px;
    }
  }

  .bottom {
    .cards_wripper {
      width: 100%;
      align-items: center;
      flex-direction: column;
    }
  }
}

.authorization {
  padding-top: 61px;
  padding-bottom: 80px;

  .authorization_wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
  }

  .authorization_title {
    width: 58%;
    letter-spacing: 0.5px;
    font-family: Ferry;
    font-size: 40px;
  }

  .authorization_img {
    position: absolute;
    top: 31%;
    left: 9%;
  }

  .form {
    padding: 27px 24px 41px 23px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    margin-left: 50px;
    width: 33%;
    font-size: 16px;
    color: #949494;

    .form_buttons {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;

      & button {
        display: flex;
        justify-content: center;
        border: none;
        background: inherit;
        font-size: 16px;
        position: relative;

        &::before {
          content: "";
          display: inline-block;
          width: 90%;
          height: 2px;
          position: absolute;
          bottom: -12px;
        }
      }

      & button:nth-child(1) {
        width: 40%;
        color: #029491;

        &::before {
          background: #029491;
        }
      }

      & button:nth-child(2) {
        width: 61%;
        color: #C7C7C7;

        &::before {
          background: #C7C7C7;
        }
      }
    }

    .form_center {
      margin-top: 52px;

      & label {
        display: block;

        & input {
          font-size: 16px;
          margin-top: 15px;
          border-radius: 5px;
          display: block;
          width: 100%;
          padding: 8px 0px 12px 15px;
          border: 1px solid #C7C7C7;
        }

      }

      & label:nth-child(2) {
        margin-top: 20px;
      }

      .form_button {
        margin-top: 34px;
        margin-bottom: 15px;
        width: 100%;
        border: none;
        border-radius: 5px;
        color: #ffffff;
        font-size: 20px;
        font-weight: 500;
        padding: 18px 0px;
        background: #5970FF;
      }

      .refresh_link {
        display: block;
        color: #5970FF;
        font-size: 14px;
        text-align: center;
        width: 100%;
        margin-bottom: 30px;
      }
    }

    .form_buttom {
      & span {
        display: block;
        margin-bottom: 15px;
      }

      .form_ul {
        display: flex;
        list-style: none;

        & li {
          display: flex;
          align-items: center;
          justify-content: center;
          border: 1px solid #4285F4;
          text-align: center;
          width: 25%;
          margin-left: 2%;
          height: 31px;

          & a {
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
      }
    }
  }
}

@media (max-width: 1016px) {
  .container {
    max-width: 850px;
  }

  .top {
    .top_wrapper {
      .top_left {
        width: 56%;

        .top_title {
          font-size: 37px;
        }

        .top_text {
          margin-bottom: 22px;
          margin-top: 17px;
          font-size: 21px;
          width: 78%;
        }
      }

      .top_right {
        background-size: 100%;
      }
    }
  }
}

@media (max-width: 873px) {
  .container {
    max-width: 700px;
  }

  .top {
    .top_wrapper {
      .top_left {
        width: 56%;

        .top_title {
          font-size: 30px;
        }

        .top_text {
          margin-bottom: 22px;
          margin-top: 17px;
          font-size: 17px;
          width: 78%;
        }
      }

      .top_right {
        background-size: 100%;
      }
    }
  }

  .title {
    font-size: 30px;
  }

  .center {

    .center_bottom {
      max-height: 350px;
      margin-top: 9px;
    }
  }
}

@media (max-width: 729px) {
  .container {
    max-width: 500px;
  }

  .top {
    padding-top: 20px;

    .top_wrapper {
      flex-direction: column;

      .top_left {
        width: 100%;

        .top_title {
          letter-spacing: -1px;
          font-size: 28px;
        }

        .top_text {
          margin-bottom: 32px;
          margin-top: 19px;
          font-size: 18px;
          width: 67%;
          min-width: 324px;
        }
      }

      .top_right {
        margin: 0 auto;
        min-width: 347px;
        min-height: 327px;
        max-height: 362px;
        position: static;
        background-size: 100%;
      }
    }
  }

  .center {
    padding-top: 0px;

    .center_bottom {
      width: 100%;
      max-height: 418px;
      background-size: 891px;
      margin-top: 9px;
    }
  }

  .title {
    font-size: 28px;
    min-width: 274px;
    width: 274px;
  }
}

@media (max-width: 866px) {
  .slick-slider {
    margin: 0 auto;
    width: 65%;
  }
}

@media (max-width: 727px) {
  .slick-slider {
    width: 100%;
  }
}